/* Component containers
----------------------------------*/

.ui-widget {
  font-family: Arial,Helvetica,sans-serif;
  font-size: 1em;
  .ui-widget {
    font-size: 1em;
  }
  input, select, textarea, button {
    font-family: Arial,Helvetica,sans-serif;
    font-size: 1em;
  }
  &.ui-widget-content {
    border: 1px solid #c5c5c5;
  }
}

.ui-widget-content {
  border: 1px solid #dddddd;
  background: #ffffff;
  color: #333333;
